import { suits, ranks, compare } from "@/models/card";
import { find, without } from "lodash-es";

export class Hand {
  constructor(cards = []) {
    this.cards = cards;
    this.sort();
    this.isReParty1 = find(this.cards, { suit: suits.spades, rank: ranks.queen });
    this.isReParty2 = find(this.cards, { suit: suits.spades, rank: ranks.ace })
  }

  isRe() {
    return !!this.isReParty1 || !!this.isReParty2;
  }

  isKontra() {
    return !this.isRe();
  }

  value() {
    return this.cards.reduce((acc, card) => acc + card.value, 0);
  }

  find(card) {
    return find(this.cards, card);
  }

  remove(card) {
    if (!this.find(card)) {
      throw new Error("can't remove card that isn't on hand");
    }

    this.cards = without(this.cards, card);
  }

  sort() {
    return this.cards.sort(compare).reverse();
  }
}
