import { includes, last } from "lodash-es";
import { re, kontra } from "@/models/parties";

export class Scorecard {
  constructor(players) {
    this.players = players;
    this.scoreLines = [];
  }

  addScore(winningPlayers, losingPlayers, winningParty, losingParty, points) {
    const newTotalPoints = {};
    this.players.forEach(player => {
    	if (winningParty === re && includes(winningPlayers, player)) {
    		newTotalPoints[player.id] = this.totalPointsFor(player) + points; 
    	}
    	if (winningParty === kontra && includes(winningPlayers, player)) {
    		newTotalPoints[player.id] =  this.totalPointsFor(player) + 0; 
    	}
    	if (losingParty === kontra && includes(losingPlayers, player)) {
    		newTotalPoints[player.id] = this.totalPointsFor(player) - 0; 
    	}
    	if (losingParty === re && includes(losingPlayers, player)) {
    		newTotalPoints[player.id] = this.totalPointsFor(player) - points; 
    	}
    });

    const scoreline = new Scoreline(newTotalPoints, winningPlayers, points);
    this.scoreLines.push(scoreline);
  }

  totalPointsFor(player) {
    if (!last(this.scoreLines)) {
      return 0;
    }
    return last(this.scoreLines).totalPoints[player.id];
  }
}

let lineIndex = 0;
export class Scoreline {
  constructor(totalPoints, winners, points) {
    this.id = lineIndex++;
    this.totalPoints = totalPoints;
    this.winners = winners;
    this.points = points;
  }
}
